<template>
  <div class="user-list">
    <div class="table-title-box">
      <span>数据列表</span>
      <router-link to="/mba/edit" tag="el-button">添加</router-link>
    </div>
    <el-table
      :data="tableData"
      border
      style="width: 100%">
      <el-table-column
        prop="sortId"
        label="序号"
        width="100">
      </el-table-column>
      <el-table-column
        prop="teamName"
        label="队伍名称"
        width="280">
      </el-table-column>
      <el-table-column
        prop="teamTitle"
        label="方案名称">
      </el-table-column>
      <el-table-column
        fixed="right"
        label="操作"
        width="200">
        <template slot-scope="{row}">
          <router-link :to="{path: '/mba/edit', query:{id: row.id}}" class="table-link mr15" tag="span">编辑</router-link>
          <span class="table-link" @click="delMba(row.id, row.teamName)">删除</span>
        </template>
      </el-table-column>
    </el-table>
    <div class="pagination-container page-box">
      <el-pagination
        class="fr"
        :current-page="currentPage"
        :page-sizes="[5, 10, 15, 20, 30, 40]"
        :page-size="pageSize"
        :total="pageTotal"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        prev-text="上一页"
        next-text="下一页"
        layout="total, sizes, prev, pager, next, jumper">
      </el-pagination>
    </div>
  </div>
</template>
<script>
import { getMbaList, delMbaTeam } from '@/service'
export default {
  data () {
    return {
      tableData: [], // mba列表数据

      // 分页器配置
      currentPage: 1,
      pageSize: 10,
      pageTotal: null
    }
  },
  methods: {
    /**
     * @description [获取mba列表数据]
     */
    getMbaData () {
      const self = this
      var dataAjax = {
        pageNum: this.currentPage,
        pageSize: this.pageSize,
        activityId: 1031
      }
      getMbaList(dataAjax).then(res => {
        if (res.status === 'true') {
          self.tableData = res.info.result
          res.info.result.map((v, i) => {
            v.sortId = ((self.currentPage - 1) * self.pageSize) + (i + 1)
          })
          self.pageTotal = res.info.total
        } else {
          self.$message.error(res.msg)
        }
      })
    },
    handleClick (row) {
      console.log(row)
    },
    /**
     * @description [删除]
     * @param       {[type]}      id   [数据id]
     * @param       {[type]}      name [队伍名称]
     */
    delMba (id, name) {
      this.$confirm(`您确定要删除队伍名称为“${name}”的这条数据吗`, '提示', {
        type: 'warning'
      }).then(() => {
        delMbaTeam({id: id}).then(res => {
          if (res.status === 'true') {
            this.$message({type: 'success', message: '删除成功！'})
            this.getMbaData()
          } else {
            this.$message.error(res.msg)
          }
        })
      }).catch(() => {
        this.$message({type: 'info', message: '已取消删除'})
      })
    },
    // 分页器事件
    handleSizeChange (val) {
      // console.log(`每页 ${val} 条`)
      this.pageSize = val
      this.getMbaData()
    },
    handleCurrentChange (val) {
      this.currentPage = val
      this.getMbaData()
    }
  },
  mounted () {
    this.getMbaData()
  }
}
</script>

<style lang="less" scoped>
  .user-list{
    margin: 40px;
  }
</style>



